import React, { useState } from 'react';
import { Button, Form, Container, Header } from 'semantic-ui-react';
import axios from 'axios';
import './App.css';

function App() {
	const [name, setName] = useState('');
	const [age, setAge] = useState('');
	const [salary, setSalary] = useState('');
	const [hobby, setHobby] = useState('');

	const handleSubmit = (e) => {
		e.preventDefault();

		const obt = { name, age, salary, hobby };

		axios
			.post(
				'https://sheet.best/api/sheets/29672e54-8817-4cbe-9e37-85f85f6199d2',
				obt
			)
			.then((response) => {
				console.log(response);
			});
	};

	return (
		<Container fluid className="container">
			<div className="h2">Ekagra's Survey</div>
			<Form>
				<div className="shape">
					<Form.Field>
						<div className="label">
							<label>Name</label>
						</div>
						<br></br>
						<div className="input">
							<input
								placeholder="Enter your Name"
								onChange={(e) => setName(e.target.value)}
							/>
						</div>
					</Form.Field>
					<br></br>
					<Form.Field>
						<div className="label">
							<label>Age</label> </div>
						<br></br>
						<div className="input">
							<input
								placeholder="Enter your Age"
								onChange={(e) => setAge(e.target.value)}
							/>
						</div>
					</Form.Field>
					<br></br>

					<Form.Field>
						<div className="label">
							<label>College you go to</label> </div>
						<br></br>
						<div className="input">
							<input
								placeholder="Enter your College"
								onChange={(e) => setSalary(e.target.value)}
							/>
						</div>
					</Form.Field>
					<br></br>
					<Form.Field>
						<div className="label">
							<label>Favorite Icecream</label> </div>
						<br></br>
						<div className="input">
							<input
								placeholder="Enter your favorite icecream"
								onChange={(e) => setHobby(e.target.value)}
							/>
						</div>
					</Form.Field>
					<br></br>

					<Button color="black" type="submit" onClick={handleSubmit}>
						Submit
					</Button>
				</div>
			</Form>
		</Container>
	);


}


export default App;